// vuex状态管理
// 状态数据=共享数据 方便其他组件使用
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

// 全局注册
Vue.use(Vuex);

// 创建Store对象
const store = new Vuex.Store({
    // 状态数据
    state: {
    // 主题分类数据
    topics:[
        { name:'all', desc:'全部' },
        { name:'good', desc:'精华' },
        { name:'share', desc:'分享' },
        { name:'ask', desc:'问答' },
        { name:'job', desc:'招聘' },
        { name:'dev', desc:'测试' }
      ],
    
    // 用户未登录时显示的更多菜单
    unLoginedMoreMenus:{
        login:'登录'
    },
      // 用户登录后显示的更多菜单
      loginedMoreMenus:{
        create:'发表新话题',
        center:'个人中心',
        msg:'消息通知',
        logout:'退出登录',
      },
      // 控制用户是否登录
      isLogined:false,
      // 登录的用户名
      loginname:'',
      // 登录用户的accesstoken
      accesstoken:'',
      // 登录用户的头像
      avatar_url: '',
      // 登录用户的id
      author_id:'',
      // 是否显示更多菜单
      isShowMoreMenus:false
    
    },
    // 状态数据的计算属性
    getters: {
        moreMenus:function(state) {
          if (state.isLogined) {
            // 用户处于登录状态，返回登陆后的更多菜单
            return state.loginedMoreMenus;
          }
          return state.unLoginedMoreMenus;
        },
        // 传入英文 返回汉字
        getTopicDescByTab: function(state){
          // 需要返回一个函数结构
          return function(tab){
          for (var index = 0; index < state.topics.length; index++){
            if(state.topics[index].name == tab){
              return state.topics[index].desc;
            }
          }
        }
        }
    },
    mutations,
    actions
});

// 对外输出模块
export default store;


