
import { mapMutations } from 'vuex';
import Msg from 'vux/src/components/msg/index.vue'


export default {
name:'logout',
data(){
return{
isShowMsg: false,
buttons:[
{
type:'primary',
text:'跳转到登录页面',
link:'/login'
}
]
}
},

created: function(){

if(this.$store.state.isLogined == false){
this.isShowMsg = true;
return;
}

this.mutationLogout();

window.localStorage.clear();

this.$router.push('/topiclist/all')
},
methods: {
...mapMutations(['mutationLogout'])
},
components:{
Msg
}
}



