
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'



import { mavonEditor } from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';


export default {
name:'edit',
props:['id'],
components: {
Group,
XTextarea,
XButton,
mavonEditor
},
data: function(){
return{
topic: {
title: '',
content:''
},

toolbars:{
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线
strikethrough: true, // 中划线
quote: true, // 引用
ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
table: true, // 表格
fullscreen: true, // 全屏编辑
readmodel: true, // 沉浸式阅读
htmlcode: true, // 展示html源码
undo: true, // 上一步
redo: true, // 下一步
trash: true, // 清空
save: true, // 保存（触发events中的save事件）
navigation: true, // 导航目录
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
preview: true, // 预览
}

}
},
methods: {

uploadImg:function(pos,file){





},
edittopic:function(){
var app = this;
console.log(this.topic.id);
console.log(this.topic.title);
console.log(this.topic.content);
console.log(this.$store.state.accesstoken);
this.$http.post('/topics/update',{
topic_id: this.topic.id,
title: this.topic.title,
content: this.topic.content,
accesstoken: this.$store.state.accesstoken,
tab:'dev'
}).then(function(response){
app.$router.back();
})
}
},
beforeRouteEnter:function(to, from, next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id).then(function(response){
vm.topic = response.data.data;
});
});
},
beforeRouteUpdate: function(to, from, next){
var app = this;
app.$http.get('/topic/'+to.params.id).then(function(response){
app.topic = response.data.data;
});
}
}



